rder: solid 2rem $color-background;\n    flex-basis: 80%;\n}\n\n.main-title {\n    align-items: center;\n    font-size: 3rem;\n}\n","@mixin media-narrowing($media-narrowing) {\n    @media (max-width: $media-narrowing) {\n        @content;\n    }\n}\n\n@mixin desktop-small-width {\n    @media (max-width: $desktop-small-width) {\n        @content;\n    }\n}\n\n@mixin media-tablet {\n    @media (max-width: $tablet-width) {\n        @content;\n    }\n}\n\n@mixin mobile-big-width {\n    @media (max-width: $mobile-big-width) {\n        @content;\n    }\n}\n@mixin mobile-small-width {\n    @media (max-width: $mobile-small-width) {\n        @content;\n    }\n}\n","@mixin heading-1($color: $color-text, $style: normal) {\n    font: {\n        family: 'Open Sans', sans-serif;\n        size: 1.5rem;\n        style: $style;\n        weight: 500;\n    }\n\n    line-height: 105%;\n    color: $color;\n}\n\n@mixin heading-2($color: $color-text, $size: 3.6rem, $style: normal) {\n    font: {\n        family: 'Open Sans', sans-serif;\n        size: $size;\n        style: $style;\n        weight: 500;\n    }\n\n    line-height: 105%;\n    color: $color;\n}\n\n@mixin basic-text($color: $color-text, $size: 3.4rem) {\n    font: {\n        family: 'Quicksand', sans-serif;\n        size: $size;\n        style: normal;\n        weight: 600;\n    }\n\n    line-height: 105%;\n    color: $color;\n}\n\n@mixin input-placeholder($color: $color-border, $style: normal) {\n    font: {\n        family: 'Quicksand', sans-serif;\n        size: 1.2rem;\n        style: $style;\n        weight: 400;\n    }\n\n    line-height: 105%;\n    color: $color;\n    padding: 0.2rem;\n}\n\n@mixin input-text($color: $color-link, $size: 1.5rem) {\n    font: {\n        family: 'Quicksand', sans-serif;\n        size: $size;\n        style: normal;\n        weight: 600;\n    }\n\n    line-height: 105%;\n    color: $color;\n}\n\n@mixin button-text($color: $color-primary, $style: normal) {\n    font: {\n        family: 'Quicksand', sans-serif;\n        size: 1.8em;\n        style: $style;\n        weight: 800;\n    }\n\n    line-height: 105%;\n    color: $color;\n}\n\n@mixin input() {\n    width: 21.4rem;\n    height: 3.3rem;\n    border: $color-border 1px solid;\n    box-shadow: 3px 3px 5px -2px rgba(0, 0, 0, 0.75);\n    padding-left: 0.5rem;\n    padding-bottom: 0.5rem;\n    cursor: pointer;\n    @include input-text;\n    &::placeholder {\n        @include input-placeholder($style: italic);\n    }\n}\n\n@mixin button-basic($color-bg: $color-light) {\n    display: block;\n    background-color: $color-bg;\n    border-radius: 1rem;\n    border: solid 2px $color-border;\n    text-align: center;\n    @include button-text;\n    padding: 0.7rem 1.3rem;\n    cursor: pointer;\n\n    &:hover {\n        background-color: $color-additional;\n        color: $color-light;\n    }\n\n    &.active {\n        background-color: $color-primary;\n        color: $color-additional;\n    }\n}\n",".button-basic {\n    @include button-basic;\n}\n",".header {\n    display: flex;\n    flex-direction: column;\n    align-items: center;\n    gap: 20px;\n    padding: 10px 20px;\n    background-color: $color-light;\n}\n\n.header-up {\n    display: flex;\n    width: 100%;\n    justify-content: space-between;\n    align-items: center;\n}\n\n.header-down {\n    display: flex;\n    width: 100%;\n}\n\n.search-box {\n    display: flex;\n    flex-direction: column;\n    width: 50%;\n}\n\n.header-logo {\n    font-size: 4rem;\n    font-weight: 700;\n    color: $color-primary-light;\n    cursor: pointer;\n    @include media-narrowing(800px) {\n        font-size: 2.5rem;\n    }\n}\n\n.btn-header {\n    display: flex;\n    justify-content: flex-end;\n    align-items: center;\n    gap: 10px;\n}\n\n.navigation {\n    display: flex;\n    gap: 5%;\n    @include media-narrowing(800px) {\n        gap: 1rem;\n    }\n}\n\n.navigation a {\n    display: flex;\n    width: 100%;\n    font-size: 2rem;\n    cursor: pointer;\n    white-space: