out;\n    cursor: pointer;\n}\n\n.navigation a:hover::after,\n.navigation a:focus::after {\n    width: 100%;\n}\n",".footer {\n    display: flex;\n    height: 100%;\n    padding: 20px;\n    align-items: center;\n    justify-content: space-between;\n    background-color: $color-primary-light;\n}\n\n.nav-footer li {\n    margin-bottom: 5%;\n}\n\n.footer-link {\n    font-size: 1.8rem;\n    color: $color-text;\n}\n\n.left-footer {\n    width: 170px;\n}\n\n.middle-footer {\n    display: flex;\n    align-items: center;\n    gap: 5px;\n    flex-direction: column;\n}\n\n.middle-footer label {\n    color: $color-text;\n    font-size: 1.5rem;\n}\n\n.right-footer {\n    display: flex;\n    flex-direction: column;\n    align-items: center;\n}\n\n.logo-footer {\n    color: $color-text;\n    font-size: 5rem;\n    font-weight: 700;\n}\n\n.text-footer {\n    color: $color-additional;\n    font-size: 1.5rem;\n}\n",".registration_container {\n    flex-basis: 50%;\n}\n\n.registration {\n    display: flex;\n    flex-direction: column;\n    padding: 4rem 10rem;\n    gap: 1rem;\n    justify-content: center;\n    align-items: center;\n    margin: 3rem auto;\n    border-radius: 3rem;\n    background-color: $color-light;\n    box-shadow: 10px 10px 25px -4px $color-shadow;\n    width: 65%;\n}\n\n.logo_registration {\n    background-image: url('img/apoint.png');\n    display: block;\n    width: 11.4rem;\n    height: 3.4rem;\n    background-repeat: no-repeat;\n    background-size: contain;\n}\n\n.registration__error {\n    font: {\n        family: 'Quicksand', sans-serif;\n        size: 1.2rem;\n        style: normal;\n        weight: 400;\n    }\n\n    line-height: 105%;\n    color: $color-error;\n}\n\n.label_basic {\n    display: flex;\n    gap: 1rem;\n    justify-content: center;\n    align-items: center;\n}\n\n.input_basic {\n    @include input;\n\n    &:focus {\n        background-color: #ff900893;\n        color: $color-primary;\n        border-color: $color-primary;\n        outline: 0;