00%;\n    height: 500px;\n    position: relative;\n    display: flex;\n    align-items: center;\n    justify-content: center;\n    background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.7) 100%),\n        url(/src/assets/gomel.jpg);\n    background-color: rgba(255, 255, 255, 0.7);\n    background-size: cover;\n    background-position: center center;\n}\n\n.main-title-hello {\n    font-size: 3rem;\n    position: relative;\n    z-index: 1;\n    color: $color-primary-light;\n    text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.8);\n}\n",".modal_container {\n    display: flex;\n    gap: 2rem;\n    flex-basis: 80%;\n\n    @include mobile-small-width {\n        flex-direction: column;\n    }\n}\n\n.info_modal_container {\n    display: flex;\n    gap: 2rem;\n    flex-direction: column;\n}\n\n.modal_title {\n    @include heading-2($color: $color-primary);\n    padding: 1rem 0;\n    cursor: pointer;\n\n    &:hover {\n        color: $color-primary;\n    }\n}\n\n.modal_text {\n    @include basic-text($color: $color-primary, $size: 3.4rem);\n    padding: 1rem 0;\n    cursor: pointer;\n\n    &:hover {\n        color: $color-primary;\n    }\n}\n\n.modal_image {\n    display: block;\n    width: 60%;\n    object-fit: contain;\n    border-radius: 1rem;\n\n    @include media-tablet {\n        width: 45%;\n    }\n}\n\n.button_add_basket {\n    @include button-basic($color-bg: $color-light);\n    display: flex;\n    align-items: center;\n    justify-content: center;\n    padding: 0.9rem 1.3rem;\n\n    @include media-tablet {\n        font-size: 9px;\n    }\n\n    @include mobile-big-width {\n        font-size: 7px;\n    }\n}\n",".title_checklist {\n    @include heading-2($color: $color-dark);\n    padding: 1rem 0;\n    text-align: center;\n    cursor: pointer;\n\n    &:hover {\n        color: $color-primary;\n    }\n}\n\n.title_categories {\n    @include heading-2($color: $color-dark, $size: 2.1rem);\n    padding: 1rem 0;\n}\n\n.reverse_button {\n    @include button-